:root {
  --main-light-clr: #dccdff;
  --main-dark-clr: #0d1014;
  --add-dark-clr: #27286e;
  --add-light-clr: #396eb3;
  --accent-clr: #2831ac;
  --animation: 400ms cubic-bezier(.215, .61, .355, 1);
}

*, :after, :before {
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--main-light-clr);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.search-form {
  background-color: var(--add-light-clr);
  justify-content: center;
  padding: 20px 0;
  display: flex;
}

.input-form {
  width: 300px;
  color: var(--add-dark-clr);
  border: none;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  outline: none;
  padding: 5px 10px;
  font-size: 18px;
  font-weight: 500;
}

.button-submit {
  cursor: pointer;
  transition: background-color var(--animation);
  border: none;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  outline: none;
  padding: 10px;
  font-size: 24px;
}

.button-submit:hover, .button-submit:focus {
  background-color: var(--accent-clr);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin: 50px 0;
  display: flex;
}

.photo-card {
  width: 320px;
  filter: drop-shadow(0 2px 2px rgba(0, 0, 0, .25));
  background-color: var(--main-light-clr);
  border-radius: 4px;
  transition: filter .5s cubic-bezier(.175, .885, .32, 1.275);
}

.photo-card:hover, .photo-card:focus {
  width: 320px;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .4));
}

.card-photo {
  border-radius: 4px;
}

.info {
  justify-content: space-around;
  padding: 10px 0;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  font-size: 12px;
  display: flex;
}

b {
  margin-bottom: 5px;
}

.container-button {
  justify-content: center;
  margin-bottom: 25px;
  display: flex;
}

.load-more {
  cursor: pointer;
  color: var(--main-light-clr);
  background-color: var(--accent-clr);
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 600;
  transition: background-color .5s cubic-bezier(.175, .885, .32, 1.275);
}

.load-more:hover, .load-more:focus {
  background-color: var(--add-dark-clr);
}

